import React, {useEffect, useState} from 'react';
import {Button, Grid, Rating, Typography} from '@mui/material';
import DataTable from 'components/@extended/DataTable';
import {useNavigate} from 'react-router-dom';
import {getPostList} from 'api/board';

const Review = () => {
	// [이벤트 매핑] useNavigate에서 navigate 가져오기
	const navigate = useNavigate();

	//[api 연결] data(기본값 : []), isLoading(기본값 : false)을 컴포넌트 State로 선언(useState)
	const [data, setData] = useState([]);
	const [isLoading, setLoading] = useState(false);

	//
	useEffect(() => {
		// [api 연결] 로딩 시 게시글 리스트 api 호출 함수 실행
		findPostList();
	}, []);

	const findPostList = async () => {
		// [api 연결] isLoading의 상태값 변경(로딩중이면 true, 아니면 false)
		setLoading(true);

		// [api 연결] Todo: api/board.js에서 게시글 리스트 api 호출 후 결과 받기
		//const response = await getPostList();

		// [api 연결] Todo: data에 결과 매핑하기
		//setData(response);
		//Sample Data
		setData([
			{id: 1, writer: '전무룡', type: '내 아이 돌봄', rating: '5', comment: '좋은 케어 감사합니다.'},
			{id: 2, writer: '구영진', type: '이웃 아이 돌봄', rating: '4', comment: '다음에 또 이용하고 싶어요.'},
		]);
		setLoading(false);
	};

	const moveCreatePostPage = () => {
		// [이벤트 매핑] Todo: 글쓰기 페이지로 이동(/post/write)
		navigate('/my-icare/write');
	};

	const rowClick = (e, row) => {
		// [이벤트 매핑] Todo: row 에서 게시글 id 받아오기(row.id)
		const postId = row.id;
		// [이벤트 매핑] Todo: 게시글 상세 페이지(/post/:id) 로 이동
		navigate(`/my-icare/${postId}`);
	};
	return (
		<>
			<h2>나의 후기</h2>
			<Grid container alignItems='center' spacing={1}>
				<Grid item>
					<Typography variant='h5'>내 평점 : </Typography>
				</Grid>
				<Grid item>
					<Rating value='4.4' precision={0.5} readOnly></Rating>
				</Grid>
			</Grid>
			{/* <Grid container direction='row' justifyContent='flex-end' spacing={2}>
				<Grid item>
					<Button variant='contained' onClick={moveCreatePostPage}>
						요청하기
					</Button>
				</Grid>
			</Grid> */}
			<Grid congtainer style={{marginTop: 50}} spacing={1}>
				<Grid item>
					<Typography variant='h5'>후기 리스트</Typography>
				</Grid>
				<Grid item>
					<DataTable columns={columns} rows={data} rowsPerPageOptions={[10, 20, 30]} isLoading={isLoading} rowClick={rowClick} />
				</Grid>
			</Grid>
		</>
	);
};
export default Review;

// [이벤트 매핑] DataTable에 들어갈 columns 설정
const columns = [
	{
		id: 'id',
		label: 'ID',
		width: 5,
		align: 'left',
	},
	{
		id: 'type',
		label: 'Type',
		width: 80,
		align: 'left',
	},
	{
		id: 'writer',
		label: '작성자',
		width: 60,
		align: 'left',
	},
	{
		id: 'rating',
		label: '평점',
		width: 50,
		align: 'left',
		render: (params) => {
			return <Rating value={params.rating} readOnly></Rating>;
		},
	},
	{
		id: 'comment',
		label: '코멘트',
		width: 500,
		align: 'left',
	},
];
